* {
    margin: 0;
    padding: 0;
}
.cf {
    -ms-zoom: 1;
    zoom: 1;
}
.cf::before, .cf::after {
    content: "";
    display: table;
}
.cf::after {
    clear: both;
}

.container {
    position: relative;
z-index: 2;
margin: 0 auto;
    height: auto;
    margin-top: 119px;
    min-height: 0;
    width: 878px;
    background: rgba(255,255,255,.8);
    margin-bottom: 100px;
    -webkit-box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
            box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);

}
.container::before {
    background: none repeat scroll 0 0 #000;
    content: "";
    display: block;
    height: 100%;
    left: 0;
    position: absolute;
    top: 0;
    width: 336px;
    z-index: -1;
}


header {
    position: fixed;
    display: block;
    z-index: 999;
    top: 0;
    left: 0;
    width: 100%;
    min-width: 954px;
    height: 60px;
    background: rgb(237, 238, 239);
    -webkit-box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
            box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
    -webkit-transition: top 0.25s;
    -o-transition: top 0.25s;
    transition: top 0.25s;
}

html{
    background: url("https://www.bungie.net/img/theme/bungienet/bgs/bg_codeRedeem.jpg") center top no-repeat;
    background-size: cover;
    color: rgb(30, 36, 43);
}

img {
    max-width: 100%;
    border: 0;
}

.leftPanel {
    float: left;
    background: #000;
    width: 336px;
}
.leftPanel_img {
    display: block;
    width: 336px;
    height: 542px;
}

.logo_destiny {
    display: block;
    vertical-align: middle;
    width: 400px;
    height: 60px;
    margin-right: 1.8vw;
    padding: 0;
    background: url("https://www.destinythegame.com/content/dam/atvi/bungie/destiny2/common/d2-logo-black.png") no-repeat scroll 18px 50% rgba(0, 0, 0, 0);
    float:left;
}
.logo_bungie{
display: block;
    vertical-align: middle;
    width: 120px;
    height: 60px;
    margin-right: 0;
    padding: 0;
    background: url("https://www.bungie.net/img/theme/bungienet/logo-bungie.png") no-repeat scroll 18px 50% dimgrey;
    float:right;
}

.Nav_Top {
    position: relative;
    height: 60px;
    min-width: 960px;
}

.platforms {
    list-style: none outside none;
    margin-top: 23px;
    position: relative;
    width: 440px;
}

.platforms a {
    background: #fff;
    border: 1px solid #bebebe;
    color: #303030;

    font-weight: 500;
    font-size: 12px;
    height: 26px;
    line-height: 26px;
    padding: 6px;
    text-align: center;
    text-indent: 0;
    text-transform: uppercase;
    width: 100px;
}



a {
    text-decoration: none;
    outline: 0;
    cursor: pointer;
    color: #0096db;
}

.ir {
    text-indent: -9999px;
    overflow: hidden;
    display: inline-block;
    text-align: left;
}



.platforms li {
    text-align: center;
    margin: 3px 3px 3px 3px;
    border: 0;
}

/* right Panel */
.rightPanel {
    float: left;
    width: 542px;
}

.rightPanel h1 {
    background: #479ce4;
    font-weight: 400;
    font-size: 16px;
    height: 81px;
    line-height: 32px;
    margin: 0;
    padding-top: 4px;
    position: static;
    text-transform: uppercase;
    width: 100%;
    color: #fff;
}

.rightPanel h1 span {
    display: block;
    line-height: 20px;
    margin-left: 13px;
}

.rightPanel h1 strong {
    font-size: 32px;
    text-transform: uppercase;
    font-weight: 700;
    font-weight: normal;
    display: block;
    margin-left: 13px;
}
.rightPanel h1 .portal::after {
    content: "";
    display: inline-block;
    width: 220px;
    height: 17px;
    background: url("https://www.bungie.net/img/theme/bungienet/bgs/bg_codeRedeem_portal.gif") no-repeat;
    margin-left: 10px;
    vertical-align: middle;
}

.rightPanel_Form {
    min-height: 359px;
    padding-bottom: 0;
}

.rightPanel_Form ol > li:first-child {
    display: block;
}

.rightPanel ol {
    list-style: none;
}

.rightPanel ol > li {
    border-top: 1px solid #cbcbcb;
    clear: both;
    min-height: 59px;
    padding-left: 75px;
    position: relative;
}

ul.images {
  margin: 0;
  padding: 0;
  white-space: nowrap;
  width: 100%;
  overflow-x: auto;
  background-color: transparent;

}
ul.images li {
  display: inline;
  
}
ul.images li > img{
    width: 130px;
  height: 130px;
  
  border: 2px solid #bebebe;
}
.rightPanel li.ready .ready {
    display: inline;
}

.hidden { display:none; }

.rightPanel ol .head {
    font-weight: 500;
    font-size: 16px;
    line-height: 60px;
    margin-left: -75px;
    margin-top: 0;
    min-height: 60px;
    padding-left: 75px;
}


.rightPanel ol > li.open {
    z-index: 2;
    border-bottom: 0;
    margin-top: 70px;
}

.rightPanel ol > li:first-child {
    border-top: 0;
}


.rightPanel ol > li.ready::before{
    background: none repeat scroll 0 0 #1e242b;
color: #fff;
}

.rightPanel ol > li::before {
    border-radius: 35px;
    display: block;
    font-weight: 400;
    font-size: 18px;
    height: 35px;
    left: 20px;
    line-height: 34px;
    position: absolute;
    text-align: center;
    top: 13px;
    width: 35px;
}

input[type="text"] {
  display: block;
  margin: 0;
  width: 400px;
  font-family: sans-serif;
  font-size: 18px;
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
  -webkit-box-shadow: none;
          box-shadow: none;
  border-radius: none;
}
input[type="text"]:focus {
  outline: none;
  text-align: center;
}

.style-1 input[type="text"] {
  padding: 10px;
  border: solid 1px #dcdcdc;
  -webkit-transition: border 0.3s, -webkit-box-shadow 0.3s;
  transition: border 0.3s, -webkit-box-shadow 0.3s;
  -o-transition: box-shadow 0.3s, border 0.3s;
  transition: box-shadow 0.3s, border 0.3s;
  transition: box-shadow 0.3s, border 0.3s, -webkit-box-shadow 0.3s;
  border: solid 1px #707070;
  -webkit-box-shadow: 0 0 5px 1px #969696;
          box-shadow: 0 0 5px 1px #969696;
}
button {
    margin: 30px 0px 0 9px;
    width: 400px;
    height: 50px;
    -webkit-box-shadow: 0 0 4px 1px #969696;
            box-shadow: 0 0 4px 1px #969696;
    font-size: 22px;
}
/* steps */
#step_load .head.ready {
    text-align: center;
    font-size: 22px;
    margin-top: 50px;
}
#step_load .head.ready div {
    margin-bottom: 50px;
}
#step_1::before {
    content: "1";
    border-top: 0;
}

#step_1 .head.ready {
    text-align: center;
    font-size: 22px;
}

#step_1 .head.ready span {
    margin: 0 28px;
}

#step_1 .welcome {
    margin-left: -61px;
    padding-top: 110px;
    text-align: center;
    width: 542px;
}

#step_2::before {
    content: "2";
    border-top: 0;
    
}

#step_2 .head.ready {
    text-align: center;
    font-size: 22px;
}

#step_2 .head.ready span {
    margin: 0 28px;
}

#step_2 .welcome {
    margin-left: -61px;
    padding-top: 110px;
    text-align: center;
    width: 542px;
}

@media (min-width: 1024px){
  .header img{
    height: 36px;
    width: 294px;
  }
  .half{
    height: 542px;
    width: 336px;
}
}


