.loader-icon,
.loader-icon:before,
.loader-icon:after {
  width: 150px;
  height: 150px;
  border-radius: 50%;
}

.loader-icon {
  margin: 0 auto;
  position: relative;
  background: transparent;
}

.loader-icon:before,
.loader-icon:after {
  position: absolute;
  left: 0;
  top: 0;
  content: "";
  border: 15px solid black;
  opacity: 0.5;
  -webkit-animation: 3s rotate infinite linear;
          animation: 3s rotate infinite linear;
}

.loader-icon:before {
  border-left-color: transparent;
}

.loader-icon:after {
  top: 16.5%;
  left: 16.5%;
  width: 100px;
  height: 100px;
  border-right-color: transparent;
  opacity: 0.2;
  -webkit-animation-direction: reverse;
          animation-direction: reverse;
}

@-webkit-keyframes rotate {
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes rotate {
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@-webkit-keyframes expand {
  50% {
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
  }
}

@keyframes expand {
  50% {
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
  }
}


